export default {
	data() {
		return {
			user_grade: [],
			level:0
		}
	},
	onLoad(e) {
		this.level = e.num;
		let that = this;
		uni.getStorage({
			key: 'user_grade',
			success: function(res) {
				that.user_grade = res.data;
			}
		})
	},
	methods: {

	}
}
